<template>
  <div>


        <div id="ecart2">
            <b-containe fluid>
                    <b-container id="ecart">
                        <b-row id="ecart2" align-h="center" class="font-weight-bold">
                            <h5>Liste des localisations</h5>
                        </b-row>
                        <b-input type="text" v-model="search" placeholder="Recherche une localisation"></b-input>

                        <b-row id="ecart3" align-h="center" v-for="(localisation,index) of filtreLocalisations" v-bind:key="index">

                                <b-col cols="6" >
                                           {{localisation.arrondissement}}
                                </b-col>
                                <b-col cols="6">
                                    <b-button pill variant="info" @click="localisationDetail(localisation)">Détail</b-button> 
                                    <b-button pill variant="primary" @click="localisationEdit(localisation)">Editer</b-button>
                                </b-col>
                                    <!-- <b-button pill variant="danger" @click="localisationDelet(localisation)">Supprimer</b-button>                    -->
                        </b-row>                
                    </b-container> 
            </b-containe>   
        </div>




        <!-- <h1>LocalisationList</h1>
        <div>
            <b-input type="text" v-model="search" placeholder="Recherche une localisation"></b-input>
        </div>
        <div>
            <ul>
                <li v-for="(localisation,index) of localisations" v-bind:key="index">
                    {{localisation.arrondissement}}
                    <b-button pill variant="info" @click="localisationDetail(localisation)">Detail</b-button> 
                    <b-button pill variant="primary" @click="localisationEdit(localisation)">Edit</b-button> 
                    <b-button pill variant="danger" @click="localisationDelet(localisation)">Delet</b-button>                   
                </li>
            </ul>            
        </div> -->

  </div>
</template>

<script>
export default {
    name:'LocalisationList',
    data(){
      return {
          search:""
      }
    },

    computed: {
        filtreLocalisations: function(){
            return this.localisations.filter((localisation)=> {
                return localisation.pays.toLowerCase().match(this.search.toLowerCase()) || localisation.region.toLowerCase().match(this.search.toLowerCase())
                || localisation.departement.toLowerCase().match(this.search.toLowerCase())|| localisation.arrondissement.toLowerCase().match(this.search.toLowerCase());
            } );
        },

        localisations () {
            return this.$store.getters.getLocalisationsListe;
        }
    },

    methods:{
        localisationDetail(localisation){
            this.$store.commit('setDetailLocalisation',localisation);
            this.$router.push("/localisation-detail/");
            // this.$router.push("/localisation-detail/"
            // +localisation.localisation_id+"/"+localisation.pays
            // +"/"+localisation.region+"/"+localisation.departement
            // +"/"+localisation.arrondissement);
        },
        localisationEdit(localisation){
            this.$store.commit('setDetailLocalisation',localisation);
            this.$router.push("/localisation-edit/");
        },
    }
}
</script>

<style>
    #ecart2 {
        margin-top: 2%;
    }
    #ecart3 {
        margin-top: 2%;
        background-color: rgb(236, 236, 236);
        border-radius: 10px;
        margin-left: 1px;
        margin-right: 1px;
    }

</style>